@mixin font($size, $weight, $line-height) {
  font-size: $size;
  font-weight: $weight;
  line-height: $line-height;
}

@mixin flex($flex-direction: column, $justify: flex-start, $align: stretch) {
  display: flex;
  flex-direction: $flex-direction;
  justify-content: $justify;
  align-items: $align;
}

@mixin size($width: 0px, $height: 0px) {
  width: $width;
  height: $height;
}
